<template>
  <div class="home">
    <!-- 1. 背景图片 -->
    <div class="bg" :style="{ background: `url(${bg})` }"></div>

    <!-- 2. 主体内容 -->
    <div class="content">
      <!-- 2.2 番茄滤镜 -->
      <div
        class="mask"
        :style="{
          'background-image': `url(${bg})`,
          'mask-image': `url(${mask})`,
        }"
      ></div>
    </div>

    <!-- 3.菜单 -->
    <div
      class="item"
      v-for="(item, index) in projectList"
      :key="index"
      :style="{
        top: item.top,
        left: item.left,
        right: item.right,
        bottom: item.bottom,
      }"
      @click="toDetails(item)"
    >
      <svg-icon v-if="item.icon" :icon-class="item.icon" />
      {{ item.value }}
      <div></div>
    </div>

    <!-- 4.1 音乐播放按钮 -->
    <div class="play" :class="playMusic ? 'turn' : ''" @click="over">
      <svg-icon icon-class="Music" style="width: 4em; height: 4em" />
    </div>

    <!-- 4.2 音乐 -->
    <audio
      ref="homeMusic"
      id="homeMusic"
      :src="musicUrl"
      controls="controls"
      loop="true"
      hidden="true"
    ></audio>
  </div>
</template>

<script>
import { projectList } from "./config";

export default {
  data() {
    return {
      // 配置数据
      projectList,
      // 按下鼠标的时间
      firstTime: "",
      // 抬起鼠标的时间
      lastTime: "",
      // 区分 click事件和 mousedown事件的变量
      flag: false,

      // 背景图片
      bg: require("../../assets/image/home_bg.jpg"),
      // mask图标
      mask: require("../../assets/image/tomato.png"),
      // 音乐地址
      musicUrl: require("../../assets/video/zhizu.mp3"),

      // 音乐转动变量
      playMusic: false,
    };
  },
  created() {
    this.projectList = projectList;
  },
  methods: {
    // 菜单跳转
    toDetails(key) {
      if (key.name) {
        this.$router.push({
          path: "/" + key.name,
        });
      } else {
        this.$message("开发小哥哥正在紧急建设中!敬请期待~");
      }
    },

    // 切换音乐播放效果
    over() {
      let music = document.getElementById("homeMusic");
      if (music.paused) {
        music.play();
        this.playMusic = true;
      } else {
        music.pause();
        this.playMusic = false;
      }
    },
  },
  mounted() {
    // let music = document.getElementById("homeMusic");
    // music.play();
    // this.playMusic = true;
  },
  beforeDestroy() {
    // 关闭音乐
    let music = this.$refs.homeMusic;
    if (!music.paused) {
      music.pause();
      this.playMusic = false;
    }
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/variables.scss";

.home {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  font-family: 幼圆;

  .bg {
    position: fixed;
    top: -20px;
    left: -20px;
    right: -20px;
    bottom: -20px;
    filter: blur(15px);
    background-size: cover;
    z-index: 2;
  }

  .content {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 5;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 900px;
    height: 100%;
    // 中心图标
    .mask {
      width: 240px;
      height: 240px;
      animation: move 50s infinite;
      background-size: cover;
      mask-size: cover;
      transition: width 0.5s, height 0.5s;
      &:hover {
        width: 300px;
        height: 300px;
      }
    }

    @keyframes move {
      0% {
        background-position: 0 0;
      }
      50% {
        background-position: 100% 0;
      }
    }
  }

  // 菜单
  .item {
    box-sizing: border-box;
    position: absolute;
    width: 200px;
    height: 60px;
    line-height: 60px;
    text-align: left;
    cursor: pointer;
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    border-top: 1px dashed #cccccc;
    z-index: 20;
    border-radius: 2px;
    transition: all 1.5s;

    &:hover {
      padding-left: 60px;
    }
  }

  .play {
    position: absolute;
    right: 5%;
    top: 5%;
    width: 4em;
    height: 4em;
    z-index: 20;
    cursor: pointer;
  }

  .turn {
    animation: turn 4s linear infinite;
  }

  @keyframes turn {
    0% {
      -webkit-transform: rotate(0deg);
    }
    25% {
      -webkit-transform: rotate(90deg);
    }
    50% {
      -webkit-transform: rotate(180deg);
    }
    75% {
      -webkit-transform: rotate(270deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
    }
  }
}
</style>